
body,div,p,ul,ol,dl,dt,dd,li,form,input,table,th,td,img,h1,h2,h3,h4,h5,h6 { margin: 0;padding: 0;
}
body { color: #000; font-size: 12px; font-style: normal; font-family:'Microsoft Yahei','\5FAE\8F6F\96C5\9ED1','Simsun','\5B8B\4F53','Arial', sans-serif;
}
em, b, i { font-style:normal;
}
li { list-style:none;
}
a,img { border:none;
}
a { text-decoration:none; color:#244666;
}
table { border-spacing:0; border-collapse:collapse;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  min-width: 1280px;
}

.lead_wrap {
  width: 100%;
  height: 50px;
  line-height: 50px;
  background-color: #000;
  font-size: 20px;
  padding: 0px 150px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff;
}
.lead_wrap a {
  color: #fff;
  margin-left: 80px;
}
.lead_wrap .router-link-active {
  color: #ffcd32;
}
.lead_wrap .author {
  float: right;
  font-size: 16px;
}
.lead_wrap .author span {
  margin-left: 20px;
}